import protoRootJson from './index.json';

export const a3 = processProto(protoRootJson.lookup('a3'));
export const atlas = processProto(protoRootJson.lookup('atlas'));
export const bach = processProto(protoRootJson.lookup('bach'));
export const blink = processProto(protoRootJson.lookup('blink'));
export const callosum = processProto(protoRootJson.lookup('callosum'));
export const common = processProto(protoRootJson.lookup('common'));
export const falcon = processProto(protoRootJson.lookup('falcon'));
export const net = processProto(protoRootJson.lookup('net'));
export const sage = processProto(protoRootJson.lookup('sage'));
export const scheduler = processProto(protoRootJson.lookup('scheduler'));
export const util = processProto(protoRootJson.lookup('util'));
export const export_service = processProto(protoRootJson.lookup('export_service'));

function processProto(protoRoot) {
    if(!protoRoot) {
        return;
    } else if (protoRoot.values) {
        return protoRoot.values;
    }

    let result = protoRoot.ctor || {};

    if(protoRoot.nestedArray) {
        protoRoot.nestedArray.forEach((nestedProtoItem) => {
            result[nestedProtoItem.name] = processProto(nestedProtoItem);
        });
    }

    return result;
}
